.profile {

  width: 90%;
  max-width: 1000px;
  margin: 1rem auto;
  padding: 20px;
  border: 7px outset lightblue;
  border-radius: 10px;
}

.profile__search {
  display: flex;
  padding: 15px;
  justify-content: space-around;
  border: 1px inset lightblue;
  border-radius: 10px;
}

.profile__input {
  width: 65%;
  padding: 15px;
  font-size: 30px;
  text-align: center;
  border: 1px outset lightblue;
  border-radius: 10px;
}

.profile__input:focus {
  outline: 2px solid lightblue;
}

.profile__button {
  width: 30%;
  padding: 10px;
  background-color: lightblue;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
}

.profile__button:hover {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  outline: 2px solid rgb(23, 155, 199);
}

.profile__info {
  padding: 15px;
}

.profile__img {
  height: 170px;
  aspect-ratio: 1/1;
  border: 3px dotted rgb(23, 155, 199);
  border-radius: 50%;
}

.profile__figcaption {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  padding: 3px;
}

.profile__name {
  font-size: 1.5rem;
  text-shadow: -1px -1px 0 #000,
    1px -1px 0 #000,
    -1px 1px 0 #000,
    1px 1px 0 #000;
  text-align: center;
  display: inline-block;
  margin: 0 auto;
  font-weight: bolder;
  font-family: sans-serif;
  color: lightblue;
  border-radius: 5%;
}

.profile__bio {
margin-top: 1rem;
font-weight: bold;
font-style: italic;
}

.profile__details {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 10px;
}

.error__p {
  font-weight: bold;
  margin-bottom: 1.5rem;
}

.loading,
.error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  color: rgb(23, 155, 199);
}

.loading-indicator {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: lightblue;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.4;
    transform: scale(0.8);
  }

  50% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0.4;
    transform: scale(0.8);
  }
}